.header{
 
  text-align: center;
  min-height: 10vh;

 width: 100%;
}
.container{
   background: cornflowerblue;
    padding: 20px 0;
  width: 100%;
  position: fixed;
}
.header button{
  width: 200px;
  height: 35px;
  background: red;
  color: white;
  border:none;
  font-size: 20px;
  border-radius: 20px;

}
.header input{
  width: 400px;
  height: 35px;
  margin-right: 20px;
  font-size: 22px;
  border-radius: 20px;
}
.App{

  background:#f2f2f2;
  
}

.recipes{
  width: 90%;
  margin:30px auto 0 auto;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  text-align: center;
  grid-gap: 50px;

}
.recipe{
  background: white;
  box-shadow: 1px 1px 5px grey,inset 1px 1px 5px grey;
  padding: 30px;
}
.recipe img{
  border-radius: 50%;
}
.recipe li{
  font-size: 20px;

}
ol{
  text-align: left;
}